<template>
  <Container :title="title">
    <v-form @submit="submit">
      <template slot="base">
        <group :disabled="true" :label="lang.username" type="input" v-model="obj.username" />
        <group :label="lang.auth_group">
          <template v-for="vol in group_array">
            <checkbox :value="vol['id']" v-model="obj.group_array" />
            <span style="margin: 0 10px 0 3px">{{vol['name']}}</span>
          </template>
        </group>

      </template>

    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group, Checkbox} from "@/components"

  export default {
    components: {Checkbox, Group, VForm, Container },
    data(){
      return {
        title:this.$t('auth.editTitle'),
        obj:{
          uid:0,
          username: '',
          group_array: [],
        },
        group_array: [],
        lang:this.$t('auth'),
      }
    },
    created(){
      this.$store.dispatch('user/fetchGroupList').then( resp =>{
        if (resp) {
          console.log(resp.data)
          this.group_array = resp.data
        }
      })
      const uuid = this.$route.query['id']
      this.$store.dispatch('user/fetchUserAuth', uuid).then( resp =>{
        if (resp) {
          console.log(resp.data)
          this.obj = resp.data
        }
      })
    },
    methods:{
      submit(){
        this.$store.dispatch('user/editAuth', this.obj).then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



